export default {
  name: 'filterMenu',
  props:{
    filterOpen       :Boolean,
  },
  data: () => ({
    stateActive      : true,
    typeActive       : true,
    sateId           : 0,    // 아이템 상태 정렬 아이디
    state:[
      {name:'전체 상품'},
      {name:'진행중 상품'},
      {name:'종료 상품'},
      {name:'새 상품'},
      {name:'인기 상품'},
      {name:'일별 거래량'},
      {name:'낮은 가격'},
      {name:'높은 가격'}
    ],
    type:[
      {name:'유튜브'},
      {name:'일러스트'},
      {name:'사진'},
      {name:'영상'},
      {name:'음악'},
    ],
  }),
  methods: {
    stateBtn(idx){
      this.sateId = idx;
    },
    // 아이템 상태 열기
    itemState(){
      this.stateActive == true ?
      this.stateActive =  false:
      this.stateActive =  true;
    },
    // 아이템 타입 열기
    itemType(){
      this.typeActive == true ?
      this.typeActive =  false:
      this.typeActive =  true
    },
    // 모달창 닫기 버튼
    closeBtn(){
      this.$emit('close')
    },
  }
}
